<template>
  <div>
    <el-table
      :data="departments.filter(data => !search || data.ename.toLowerCase().includes(search.toLowerCase()))"
      style="width: 100%">
      <el-table-column
        prop="sid"
        label="ID ">
      </el-table-column>
      <el-table-column
        prop="ename"
        label="">
      </el-table-column>
      <el-table-column
        prop="emanager"
        label="">
      </el-table-column>
      <el-table-column
        prop="eintro"
        label="">
      </el-table-column>
      <el-table-column align="right">
        <!--eslint-disable-next-line vue/no-unused-vars-->
        <template slot="header" slot-scope="scope">
          <el-input
            type="text"
            v-model="search"
            size="mini"
            placeholder=""/>
        </template>
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="editDepartment(scope.row)"></el-button>
          <el-button
            size="mini"
            type="danger"
            @click="deleteDepartment(scope.row.sid)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-button style="margin:15px;float:right" type="primary" @click="editDepartment()" round></el-button>
    <el-dialog title="/" :visible.sync="editFormVisible" @close="clear">
      <el-form v-model="editForm">
          <el-form-item label="" prop="ename">
            <el-input v-model="editForm.ename" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="emanager">
            <el-input v-model="editForm.emanager" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="eintro">
            <el-input v-model="editForm.eintro" autocomplete="off"></el-input>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false"> </el-button>
        <el-button type="primary" @click="onSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
 export default {
    data () {
      return {
        departments: [], 
        editForm: [],
        search: "",
        editFormVisible: false
      }
    },
    mounted: function () {
      this.loadDepartments()
    },
    methods: {
      loadDepartments () {
        this.axios.get('/departments').then(response => {
          if (response && response.status === 200) {
            this.departments = response.data
          }
        })
      },
      deleteDepartment (sid) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            this.axios
              .post('/departmentsdelete', {sid: sid}).then(resp => {
              if (resp && resp.status === 200) {
                this.loadDepartments()
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      editDepartment (department) {
        this.editFormVisible = true
        //console.log(department.sid);
        this.editForm = {
          sid: department.sid,
          ename: department.ename,
          emanager: department.emanager,
          eintro: department.eintro
        }
      },
      onSubmit () {
        //console.log(this.editForm.ename);
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            this.axios
              .post('/departments', {
                sid: this.editForm.sid,
                ename:this. editForm.ename,
                emanager: this.editForm.emanager,
                eintro: this.editForm.eintro
              }).then(resp => {
              if (resp && resp.status === 200) {
                this.editFormVisible = false
                this.loadDepartments()
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      clear () {
        this.editForm = {
          sid: '',
          ename: '',
          emanager: '',
          eintro: ''
        }
      }
    }
 }
</script>
