<template>
    <div>
        <el-table :data="training.filter(data => !search || data.sname.toLowerCase().includes(search.toLowerCase()))"
                  style="width: 100%">
            <el-table-column prop="sid"
                             label="ID ">
            </el-table-column>
            <el-table-column prop="sname"
                             label="">
            </el-table-column>
            <el-table-column prop="sremark"
                             label="">
            </el-table-column>
            <el-table-column align="right">
                <!--eslint-disable-next-line vue/no-unused-vars-->
                <template slot="header" slot-scope="scope">
                    <el-input type="text"
                              v-model="search"
                              size="mini"
                              placeholder="" />
                </template>
                <template slot-scope="scope">
                    <el-button size="mini"
                               @click="editTraining(scope.row)"></el-button>
                    <el-button size="mini"
                               type="danger"
                               @click="deleteTraining(scope.row.sid)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-button style="margin:15px;float:right" type="primary" @click="editTraining()" round></el-button>
        <el-dialog title="/" :visible.sync="editFormVisible" @close="clear">
            <el-form v-model="editForm">
                <el-form-item label="" prop="sname">
                    <el-input v-model="editForm.sname" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="sremark">
                    <el-input v-model="editForm.sremark" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editFormVisible = false"> </el-button>
                <el-button type="primary" @click="onSubmit"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                training: [],
                editForm: [],
                search: "",
                editFormVisible: false
            }
        },
        mounted: function () {
            this.loadTrainings()
        },
        methods: {
            loadTrainings() {
                this.axios.get('/trainings').then(response => {
                    if (response && response.status === 200) {
                        this.training = response.data
                    }
                })
            },
            deleteTraining(sid) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.axios
                        .post('/trainingsdelete', { sid: sid }).then(resp => {
                            if (resp && resp.status === 200) {
                                this.loadTrainings()
                            }
                        })
                }
                ).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    })
                })
            },
            editTraining(training) {
                this.editFormVisible = true
                //console.log(training.sid);
                this.editForm = {
                    sid: training.sid,
                    sname: training.sname,
                    sremark: training.sremark
                }
            },
            onSubmit() {
                //console.log(this.editForm.sname);
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.axios
                        .post('/trainings', {
                            sid: this.editForm.sid,
                            sname: this.editForm.sname,
                            sremark: this.editForm.sremark
                        }).then(resp => {
                            if (resp && resp.status === 200) {
                                this.editFormVisible = false
                                this.loadTrainings()
                            }
                        })
                }
                ).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    })
                })
            },
            clear() {
                this.editForm = {
                    sid: '',
                    sname: '',
                    sremark: ''
                }
            }
        }
    }
</script>
