<template>
  <div>
    <el-table
      :data="users.filter(data => !search || data.username.toLowerCase().includes(search.toLowerCase()))"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="ID ">
      </el-table-column>
      <el-table-column
        prop="username"
        label="">
      </el-table-column>
      <el-table-column
        prop="password"
        label="">
      </el-table-column>
      <el-table-column
        prop="sid"
        label="">
      </el-table-column>
      <el-table-column align="right">
        <!--eslint-disable-next-line vue/no-unused-vars-->
        <template slot="header" slot-scope="scope">
          <el-input
            type="text"
            v-model="search"
            size="mini"
            placeholder=""/>
        </template>
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="editUser(scope.row)"></el-button>
          <el-button
            size="mini"
            type="danger"
            @click="deleteUser(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-button style="margin:15px;float:right" type="primary" @click="editUser()" round></el-button>
    <el-dialog title="/" :visible.sync="editFormVisible" @close="clear">
      <el-form v-model="editForm">
          <el-form-item label="" prop="username">
            <el-input v-model="editForm.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="editForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sid">
            <el-input v-model="editForm.sid" autocomplete="off"></el-input>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false"> </el-button>
        <el-button type="primary" @click="onSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
 export default {
    data () {
      return {
        users: [], 
        editForm: [],
        search: "",
        editFormVisible: false
      }
    },
    mounted: function () {
      this.loadUsers()
    },
    methods: {
      loadUsers () {
        this.axios.get('/users').then(response => {
          if (response && response.status === 200) {
            this.users = response.data
          }
        })
      },
      deleteUser (sid) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            this.axios
              .post('/usersdelete', {sid: sid}).then(resp => {
              if (resp && resp.status === 200) {
                this.loadUsers()
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      editUser (user) {
        this.editFormVisible = true
        this.editForm = {
          id: user.id,
          username: user.username,
          password: user.password,
          sid: user.sid
        }
      },
      onSubmit () {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            this.axios
              .post('/users', {
                id: this.editForm.id,
                username:this. editForm.username,
                password: this.editForm.password,
                sid: this.editForm.sid
              }).then(resp => {
              if (resp && resp.status === 200) {
                this.editFormVisible = false
                this.loadUsers()
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      clear () {
        this.editForm = {
          id: '',
          username: '',
          password: '',
          sidsid: ''
        }
      }
    }
 }
</script>
