<template>
  <el-card class="box-card slide-in-elliptic-left-fwd">
    <div slot="header" class="clearfix">
      <span></span>
      <el-button style="float: right; padding: 3px 0" type="text" @click="openchangepassword()"></el-button>
    </div>
    <div class="text item">
        {{' ' + userForm.username }}
    </div>
    <div class="text item">
        {{' ' + userForm.password }}
    </div>
    <div v-show="DivVisible">
      <div class="text item">
        {{' ' + personForm.sid }}
      </div>
      <div class="text item">
        {{' ' + personForm.sname }}
      </div>
      <div class="text item">
        {{' ' + personForm.ssex }}
      </div>
      <div class="text item">
        {{' ' + personForm.sbirthday }}
      </div>
      <div class="text item">
        {{' ' + personForm.sdepartment }}
      </div>
      <div class="text item">
        {{' ' + personForm.sjob }}
      </div>
      <div class="text item">
        {{' ' + personForm.sedu }}
      </div>
      <div class="text item">
        {{' ' + personForm.sspcialty }}
      </div>
      <div class="text item">
        {{' ' + personForm.saddress }}
      </div>
      <div class="text item">
        {{' ' + personForm.stel }}
      </div>
      <div class="text item">
        {{' ' + personForm.semail }}
      </div>
    </div>
  </el-card>
</template>

<script>
  export default {
    name: 'UserIndex',
    data () {
      return {
        personForm: {
          sid: '',
          sname: '',
          ssex: '',
          sbirthday: '',
          sdepartment: '',
          sjob: '',
          sedu: '',
          sspcialty: '',
          saddress: '',
          stel: '',
          semail: ''
        },
        userForm: {
          username: '',
          password: ''
        },
        DivVisible: false
      }
    },
    mounted: function () {
      this.judgment();
    },
    methods: {
      loadpersonForm () {
        this.axios.get('/usergetmyperson').then(response => {
          if (response && response.status === 200) {
            this.personForm = response.data
          }
          if(this.personForm.sid == null)
            {
              this.DivVisible = false;
              this.$message({
                message: '',
                type: 'warning'
              });
            }
          else
            this.DivVisible = true;
        })
      },
      loaduserForm () {
        this.axios.get('/user').then(response => {
          if (response && response.status === 200) {
            this.userForm = response.data
          }
        })
      },
      judgment () {
        this.loaduserForm();
        this.loadpersonForm();
      },
      openchangepassword () {
        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(({ value }) => {
          this.axios
            .post('/changepassword', {password: value}).then(resp => {
              if (resp && resp.status === 200) {
                this.loaduserForm();
                this.$message({
                  type: 'success',
                  message: ': ' + value
                });
              }
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });       
        });
      }
    }
  }
</script>

<style scoped>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .box-card {
    width: 480px;
    margin: 30px auto;
  }

  .slide-in-elliptic-left-fwd{animation:slide-in-elliptic-left-fwd .7s cubic-bezier(.25,.46,.45,.94) both}
  @keyframes slide-in-elliptic-left-fwd{0%{transform:translateX(-800px) rotateY(30deg) scale(0);transform-origin:-100% 50%;opacity:0}100%{transform:translateX(0) rotateY(0) scale(1);transform-origin:1800px 50%;opacity:1}}
</style>