<template>
    <div>
        <el-table :data="recruitment.filter(data => !search || data.sname.toLowerCase().includes(search.toLowerCase()))"
                  style="width: 100%">
            <el-table-column prop="sid"
                             label="ID ">
            </el-table-column>
            <el-table-column prop="sname"
                             label="">
            </el-table-column>
            <el-table-column prop="ssex"
                             label="">
            </el-table-column>
            <el-table-column prop="sbirthday"
                             label="">
            </el-table-column>
            <el-table-column prop="sedu"
                             label="">
            </el-table-column>
            <el-table-column prop="sspcialty"
                             label="">
            </el-table-column>
            <el-table-column prop="saddress"
                             label="">
            </el-table-column>
            <el-table-column prop="stel"
                             label="">
            </el-table-column>
            <el-table-column prop="semail"
                             label="">
            </el-table-column>
            <el-table-column align="right">
                <!--eslint-disable-next-line vue/no-unused-vars-->
                <template slot="header" slot-scope="scope">
                    <el-input type="text"
                              v-model="search"
                              size="mini"
                              placeholder="" />
                </template>
                <template slot-scope="scope">
                    <el-button size="mini"
                               @click="editRecruitment(scope.row)"></el-button>
                    <el-button size="mini"
                               type="danger"
                               @click="deleteRecruitment(scope.row.sid)"></el-button>
                    <el-button size="mini"
                               type="success"
                               @click="Hire(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-button style="margin:15px;float:right" type="primary" @click="editRecruitment()" round></el-button>
        <el-dialog title="/" :visible.sync="editFormVisible" @close="clear">
            <el-form v-model="editForm">
                <el-form-item label="" prop="sname">
                    <el-input v-model="editForm.sname" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="ssex">
                    <el-input v-model="editForm.ssex" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="sbirthday">
                    <el-input v-model="editForm.sbirthday" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="sedu">
                    <el-input v-model="editForm.sedu" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="sspcialty">
                    <el-input v-model="editForm.sspcialty" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="saddress">
                    <el-input v-model="editForm.saddress" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="stel">
                    <el-input v-model="editForm.stel" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="semail">
                    <el-input v-model="editForm.semail" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editFormVisible = false"> </el-button>
                <el-button type="primary" @click="onSubmit"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
 export default {
    data () {
      return {
        recruitment: [],
        editForm: [],
        search: "",
        editFormVisible: false,
      }
    },
    mounted: function () {
        this.loadRecruitments()
    },
    methods: {
        loadRecruitments () {
            this.axios.get('/recruitments').then(response => {
            if (response && response.status === 200) {
                this.recruitment = response.data
          }
        })
      },
        deleteRecruitment(sid) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            this.axios
                .post('/recruitmentsdelete', {sid: sid}).then(resp => {
              if (resp && resp.status === 200) {
                  this.loadRecruitments()
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
        editRecruitment(recruitment) {
        this.editFormVisible = true
        //console.log(recruitment.sid);
          this.editForm = {
           sid: recruitment.sid,
           sname: recruitment.sname,
           ssex: recruitment.ssex,
           sbirthday: recruitment.sbirthday,
           sedu: recruitment.sedu,
           sspcialty: recruitment.sspcialty,
           saddress: recruitment.saddress,
           stel: recruitment.stel,
           semail: recruitment.semail
        }
       },
        Hire(recruitment) {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'primary'
            }).then(() => {
                this.axios
                    .post('/personhire', {
                        sid: recruitment.sid,
                        sname: recruitment.sname,
                        ssex: recruitment.ssex,
                        sbirthday: recruitment.sbirthday,
                        sedu: recruitment.sedu,
                        sspcialty: recruitment.sspcialty,
                        saddress: recruitment.saddress,
                        stel: recruitment.stel,
                        semail: recruitment.semail
                    }).then(resp => {
                        if (resp && resp.status === 200) {
                            this.loadRecruitments()
                        }
                    })
            }
            ).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                })
            })
        },
      onSubmit () {
        //console.log(this.editForm.sname);
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            this.axios
                .post('/recruitments', {
                sid: this.editForm.sid,
                sname: this.editForm.sname,
                ssex: this.editForm.ssex,
                sbirthday: this.editForm.sbirthday,
                sedu: this.editForm.sedu,
                sspcialty: this.editForm.sspcialty,
                saddress: this.editForm.saddress,
                stel: this.editForm.stel,
                semail: this.editForm.semail
              }).then(resp => {
              if (resp && resp.status === 200) {
                this.editFormVisible = false
                  this.loadRecruitments()
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      clear () {
        this.editForm = {
          sid: '',
          sname: '',
          ssex: '',
          sbirthday: '',
          sdepartment: '',
          sjob: '',
          sedu: '',
          sspcialty: '',
          saddress: '',
          stel: '',
          semail: ''
        }
      }
    }
 }
</script>
