<template>
  <div>
    <el-table
      :data="salarys.filter(data => !search || data.sname.toLowerCase().includes(search.toLowerCase()))"
      style="width: 100%">
      <el-table-column
        prop="sid"
        label="">
      </el-table-column>
      <el-table-column
        prop="sname"
        label="">
      </el-table-column>
      
      <el-table-column
        prop="ssalary"
        label="">
      </el-table-column>
      <el-table-column align="right">
        <!--eslint-disable-next-line vue/no-unused-vars-->
        <template slot="header" slot-scope="scope">
          <el-input
            type="text"
            v-model="search"
            size="mini"
            placeholder=""/>
        </template>
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="editSalary(scope.row)"></el-button>
          <el-button
            size="mini"
            type="danger"
            @click="deleteSalary(scope.row.sid)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-button style="margin:15px;float:right" type="primary" @click="editSalary()" round></el-button>
    <el-dialog title="/" :visible.sync="editFormVisible" @close="clear">
      <el-form v-model="editForm">
          <el-form-item label="" prop="sname">
            <el-input v-model="editForm.sname" autocomplete="off"></el-input>
          </el-form-item>
         
          <el-form-item label="" prop="ssalary">
            <el-input v-model="editForm.ssalary" autocomplete="off"></el-input>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false"> </el-button>
        <el-button type="primary" @click="onSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
 export default {
    data () {
      return {
        salarys: [], 
        editForm: [],
        search: "",
        editFormVisible: false
      }
    },
    mounted: function () {
      this.loadSalarys()
    },
    methods: {
      loadSalarys () {
        this.axios.get('/salarys').then(response => {
          if (response && response.status === 200) {
            this.salarys = response.data
          }
        })
      },
      deleteSalary (sid) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            this.axios
              .post('/salarysdelete', {sid: sid}).then(resp => {
              if (resp && resp.status === 200) {
                this.loadSalarys()
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      editSalary (salary) {
        this.editFormVisible = true
        //console.log(salary.sid);
        this.editForm = {
          sid: salary.sid,
          sname: salary.sname,
          
          ssalary: salary.ssalary
        }
      },
      onSubmit () {
        //console.log(this.editForm.sname);
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            this.axios
              .post('/salarys', {
                sid: this.editForm.sid,
                sname:this. editForm.sname,
                
                ssalary: this.editForm.ssalary
              }).then(resp => {
              if (resp && resp.status === 200) {
                this.editFormVisible = false
                this.loadSalarys()
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      clear () {
        this.editForm = {
          sid: '',
          sname: '',
          
          ssalary: ''
        }
      }
    }
 }
</script>
